/*css reset*/

body,h1,p,h3{
    margin:0;
    color:#cfcfcf;
    font-family: "\5FAE\8F6F\96C5\9ED1";
    font-size:14px;
}

body{
    overflow: hidden;
    background-color: #000;
}

img{
  border:none;
  vertical-align: top;
}

ul{
    padding:0;
    margin:0;
    list-style:none;
}

a{
    color:#cfcfcf;
    text-decoration: none;
}

/*tools*/

.fl{
    float:left;
}

.fr{
    float:right;
}

.clearfix{
    *zoom:1;
}

.clearfix:after{
    content:"";
    display: block;
    clear:both;
}

/*loadbox*/

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 999999;
}

#status {
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    background-image: url(/static/img/loader.044748c.gif);
    background-repeat: no-repeat;
    background-position: center center;
    text-align: center;
    margin: -40px 0 0 -100px;
}

#status img {
    width:120px;
    height:120px;
    text-align: center;
    margin-top: -80px;
}

#status div{
    color:#000;
}

