/*header*/

#head{
    height:50px;
    background-color:rgba(3,3,3,1);
    padding:0 20px;
    line-height:50px;
    overflow: hidden;
}

#head h1{
    margin-right:60px;
}

#head a{
    padding:0 15px;
    border-right:1px solid #fff;
}

#head a:nth-last-of-type(1){
    border-right:none;
}

#head a:hover{
    color:#fff;
    cursor:pointer;
}

.user div{
  margin-left:40px;
  font-size:18px;
  cursor: pointer;
}

.nav-box i{
    margin-right:10px;
    font-size:16px;
    color:#fff;
}

/*home*/

.home{
    width:100%;
    height:100%;
    background-size:cover;
    color:#666;
    overflow: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

.home h3{
    color:#666;
}

.home .typed{
    letter-spacing: 3px;
}

.home .personinfo{
    position: absolute;
    text-align: center;
    min-width: 200px;
    min-height:160px;
    left: 50%;
    top: 50%;
    padding: 65px;
    background: rgba(255, 255, 255, 0.8);
    outline: 4px double #FFFFFF;
    outline-offset: 8px;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
}

.personinfo h3{
    font-size:32px;
    font-family: "Arial";
    margin:20px 0;
}

.tipico{
    position: absolute;
    border: 2px solid #2f2f2f;
    background: #ffffff;
    opacity: 0.8;
    border-radius: 13px;
    height: 40px;
    width: 30px;
    margin: 0 auto;
    display: block;
    left: 0;
    right: 0;
    bottom: 30px;
    z-index: 9;
}

.tipico .linescroll{
    position: relative;
    border-radius: 10px;
    background: #2f2f2f;
    width: 4px;
    height: 8px;
    top: 4px;
    margin-left: auto;
    margin-right: auto;
}

.tipico .linescroll{
    -webkit-animation-name: scrolling;
    -webkit-animation-duration: 1s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-delay: 0s;
    -webkit-animation-iteration-count: infinite;
    animation-name: scrolling;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count:infinite;
    -webkit-animation-play-state:running;
            animation-play-state:running;
}

/*content*/

#content{
    overflow: auto;
}

::-webkit-scrollbar {
    width: 16px;
    height: 10px;
    overflow: visible;
}

::-webkit-scrollbar-button {
    width: 0;
    height: 0;
}

::-webkit-scrollbar-thumb {
    background-color: #000;
    border: solid transparent;
    border-width: 1px 0 1px 8px;
    background-clip: padding-box;
}

