}

.content .todolist{
    background-color: #d9544f;
    margin:0 20px;
    height:60px;
    border-radius:10px;
    margin-top:20px;
    text-align: center;
    line-height:60px;
}

.content .main-title{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height:120px;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
