.main-title .btn{
    margin:0 20px;
    width:26%;
    height:120px;
    border:1px solid #333;
    line-height:60px;
    background-color: blue;
    border-radius:10px;
    cursor:pointer;
}

.main-title .btn:nth-of-type(1){
    background-color: #418bca;
}

.main-title .btn:nth-of-type(2){
    background-color: #16a085;
}

.main-title .btn:nth-of-type(3){
