}

.inputbox{
    text-align: center;
    height:60px;
    line-height:60px;
}

.inputbox .text{
    height:30px;
    width:600px;
    margin-right:30px;
}

.inputbox .btn{
    height:35px;
    width:80px;

}

.infolist li{
    border-bottom:1px dotted #666;
    height:60px;
    line-height:60px;
    text-indent: 20px;
    margin:0 20px;
    cursor:pointer;
    position:relative;
}

.infolist li:hover{
    color:#fff;
    background-color: rgba(0,0,0,.3);
    -webkit-transition:0.5s;
    transition:0.5s;
}

.addToDo{
  text-align: center;
  margin-top:30px;
}

.addToDo input{
  outline: none;
  vertical-align: middle;
  margin-right:20px;
  border:none;
  min-width:800px;
  height:50px;
  padding: 0 20px;
  border: none;
  border-radius:20px;
  background-color: rgba(0,0,0,.3);
  color: #aaa;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-appearance: none;
  margin-right:50px;
}

.addToDo button{
  width:100px;
  height:50px;
  vertical-align: middle;
  outline: none;
  border-radius:20px;
  border:none;
  cursor: pointer;
  background-color: #d9544f;
  color:#cfcfcf;
}

.infolist .cbtn{
    display: inline-block;
    background:url(/static/img/checkbox-animation_22.23b4d6e.png);
    height: 22px;
    width: 22px;
    margin-right: 0.5rem;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    cursor: pointer;
    position: relative;
    left: -20px;
    top: 5px;
    opacity: 0.8;
    background-position: left;
    background-size: auto 100%;
}

.infolist .move{
    -webkit-transition: background-position 0.5s steps(29, end);
    transition: background-position 0.5s steps(29, end);
    background-position: right;
    opacity: 1;
}

.line{
    width:0;
    height:2px;
    position:absolute;
    background-color: #fff;
    top:30px;
    left:50px;
    -webkit-transition:0.5s;
    transition:0.5s;
}

/*books list*/

.books .books-head{
    height:60px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-top:15px;
    overflow: hidden;
}

.books .books-title{
    height:30px;
    border-left:3px solid #fff;
    line-height:30px;
    padding-left:20px;
}

.books-title i {
    font-size:20px;
    margin-right:8px;
}

.books-list{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
    -webkit-box-align:start;
        -ms-flex-align:start;
            align-items:flex-start;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}

.books-list li{
    width:280px;
    height:340px;
    background-color: rgba(0,0,0,.2);
    margin:15px;
    -webkit-box-shadow: 0 0 10px 5px rgba(3,3,3,.2);
            box-shadow: 0 0 10px 5px rgba(3,3,3,.2);
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    padding:20px;
    overflow: hidden;
    position:relative;
    cursor:pointer;
}

.books-list li:before,
.books-list li:after {
    display: inline-block;
    position: absolute;
    content: " ";
    width: 100%;
    height: 3px;
    background: rgba(225,225,225,.3);
    -webkit-transform: scaleX(0);
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.books-list li:before {
    top: 0;
    left: 0;
    -webkit-transform-origin: 0 0;
}

.books-list li:after {
    bottom: 0;
    left: 0;
    -webkit-transform-origin: 100% 0;
}

.books-list li:hover{
     -webkit-box-shadow: 20px 20px 0px #000;
             box-shadow: 20px 20px 0px #000;
     -webkit-transform: translateY(-10px) translateX(-10px);
     -webkit-transition: all 0.2s ease 0.4s;
     transition: all 0.2s ease 0.4s;
 }

.books-list li:hover:after,
.books-list li:hover:before {
    -webkit-transform: scaleX(1);
}

.card:before,
.card:after {
    display: inline-block;
    position: absolute;
    content: " ";
    width: 3px;
    height: 100%;
    background: rgba(225,225,225,.3);
    -webkit-transform: scaleY(0);
    -webkit-transition: all 0.2s ease 0.2s;
    transition: all 0.2s ease 0.2s;
}

.card:before {
    top: 0;
    left: 0;
    -webkit-transform-origin: 0 100%;
}

.card:after {
    top: 0;
    right: 0;
    -webkit-transform-origin: 0 0;
}

.books-list li:hover .card:after,
.books-list li:hover .card:before {
    -webkit-transform: scaleY(1);
}

