/*animation*/

@-webkit-keyframes scrolling {
    0% {
        top: 2px;
        opacity: 0;
    }

    30% {
        top: 8px;
        opacity: 1;
    }
    100% {
        top: 16px;
        opacity: 0;
    }
}

@keyframes scrolling {
    0% {
        top: 2px;
        opacity: 0;
    }

    30% {
        top: 8px;
        opacity: 1;
    }
    100% {
        top: 16px;
        opacity: 0;
    }
}

